@import '../../node_modules/foundation-sites/scss/foundation';

$breakpoints: (
  small: 0,
  medium: 640px,
  mediumLarge: 900px,
  large: 1024px,
  xlarge: 1200px,
  xxlarge: 1440px,
);

$foundation-palette: (
  primary: #d8e8ff,
  secondary: #767676,
  success: #3adb76,
  warning: #ffff32,
  alert: #e50000,
);

$closebutton-color: #000;

$callout-border: none;
$callout-margin: none;
$callout-background-fade: 1%;

$dropdownmenu-arrows: false;

@include foundation-everything;

// get fontawesome
@import '../../node_modules/font-awesome/scss/font-awesome';

// get custom-designed icons as font-family by icomoon
@import '../fonts/icomoon/style.css';

// load vendor fonts
// Poppins: Regular, Semibold, Bold
@font-face {
  font-family: 'Poppins Regular';
  font-style: normal;
  font-weight: normal;
  src: url('../fonts/webfontkit-poppins-regular/poppins-regular-webfont.woff2') format('woff2'),
  url('../fonts/webfontkit-poppins-regular/poppins-regular-webfont.woff') format('woff');
}
@font-face {
  font-family: 'Poppins Semibold';
  font-style: normal;
  font-weight: normal;
  src: url('../fonts/webfontkit-poppins-semibold/poppins-semibold-webfont.woff2') format('woff2'),
  url('../fonts/webfontkit-poppins-semibold/poppins-semibold-webfont.woff') format('woff');
}
@font-face {
  font-family: 'Poppins Bold';
  font-style: normal;
  font-weight: normal;
  src: url('../fonts/webfontkit-poppins-bold/poppins-bold-webfont.woff2') format('woff2'),
  url('../fonts/webfontkit-poppins-bold/poppins-bold-webfont.woff') format('woff');
}
